<template>
    <view class="w-editor">
        <scroll-view 
            :scroll-top="scrollTop" 
            scroll-y="true" 
            class="w-editor-scroll" 
            @scroll="scroll">
            
            <view v-for="(item ,i) in data" :class="{'handle-box': item.handleData}">
                <w-editor-item 
                :key="item.index" 
                :data="item" 
                :init="initObj[i]"
                v-model="item.value"
                :read-only="readOnly"
                @upClick="handleUp(i)"
                @downClick="handleDown(i)"
                @delClick="handleDel(i)"
                @optionChange="handleOptionChange($event, i)"
                @permit="permit($event, i)"
                :upShow="i != 0" 
                :downShow="i != (data.length - 1 - (data[data.length-1].handleData ? 1 : 0))"></w-editor-item>
            </view>
            
        </scroll-view>
        <view class="w-editor-add" @click="showAddItems" v-if="!readOnly">
            <text class="w-editor-add__icon">+</text>
            <text class="w-editor-add__text">新增</text>
        </view>
        
        <u-popup :show="addItemsPopup" mode="bottom" @close="addItemsPopup = false" @open="addItemsPopup = true">
            <view class="popup">
                <view class="popup-content">
                    <view 
                    class="popup-content__item" 
                    v-for="(item, i) in addItemsArr" 
                    :key="i" 
                    :style="'width:' + (100 / moduleSpan) + '%'"
                    @click="handleAddItem(item)">
                        <i class="w_editor-icon" :class="item.icon"></i>
                        <text>{{item.label}}</text>
                    </view>
                </view>
                <view class="popup-btn">
                    <u-button @click="addItemsPopup = false">取消</u-button>
                </view>
            </view>
        </u-popup>
    </view>
</template>

<script>
    import setting from "./components/setting.js"
    import WEditorItem from './components/w-editor-item.vue'
    
    export default {
        components: {
            WEditorItem
        },
        props: {
            // 只读模式/编辑模式
            readOnly: {
                type: Boolean,
                default: false
            },
            // 不使用的模组
            hideModules: {
                type: Array,
                default: () => []
            },
            // 模组布局每行几个
            moduleSpan: {
                type: Number,
                default: 4
            },
            value: {
                type: Array,
                default: () => []
            }
        },
        data() {
            return {
                scrollTop: 0,
                old: {
                    scrollTop: 0
                },
                addItemsPopup: false,
                data: this.value,
                initObj: {}
            }
        },
        computed: {
            addItemsArr() {
                const hideModules = new Set(this.hideModules);
                let diffArr = Array.from(setting.filter(x => !hideModules.has(x.type)));
                return diffArr
            }
        },
        watch: {
            data: {
                deep: true,
                handler(e) {
                    this.$emit('input', e)
                },
            },
            value: {
                deep: true,
                handler(e) {
                    this.data = e
                },
            },
        },
        methods: {
            scroll: function(e) {
                this.old.scrollTop = e.detail.scrollTop
            },
            showAddItems() {
                this.addItemsPopup = true
            },
            handleAddItem(item) {
                const temp = [...this.data]
                const index = temp.findIndex(o => o.handleData)
                if(index>=0){
                    temp.splice(index, 1)
                }
                temp.push(
                    {
                        type: item.type,
                        value: item.value || '',
                        option: item.option || {},
                        handleData: true,
                        index: Math.random()
                    }
                )
                this.initObj[temp.length-1] = true
                this.data = temp
                this.addItemsPopup = false
            },
            permit(res, index) {
                if(res) {
                    this.data[index].handleData = false
                }else{
                    this.data.splice(index, 1)
                }
            },
            handleUp(index) {
                if(index != 0) {
                    this.data[index] = this.data.splice(index-1, 1, this.data[index])[0];
                }
            },
            handleDown(index) {
                if(index != this.data.length-1) {
                    this.data[index] = this.data.splice(index+1, 1, this.data[index])[0];
                }
            },
            handleDel(index) {
                this.data.splice(index, 1)
            },
            handleOptionChange(option = {}, index) {
                this.data[index].option = option
            },
        }
    }
</script>
